$main-color: #0074D9;

.projectbkg {
    width: 100%;
    position: relative;
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    z-index: 0;

    .wave-svg {
        position: absolute;
        top: 55%;
        left: 0;
        transform: translateY(-50%);
        width: 100%;
        height: auto;
        z-index: -1;
    }

    .projects {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding-top: 100px;
        position: relative;
        z-index: 1;
    
        h2 {
            margin-bottom: 50px;
        }

        ul {
            list-style: none;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            margin-bottom: 35px;
        
            li {
                margin: 0 20px;
            }
        }

        @media (max-width: 900px) {
            ul {
                flex-direction: column; 
                align-items: center; 

                li {
                    margin: 15px 0; 
                }
            }

        }
    }
}
