$main-color: #0074D9;
$secondary-color: white;
$third-color: black;

.home {
    height: 100vh;
    width: 100%;
    overflow-y: hidden;
    background: linear-gradient(to bottom right, $secondary-color 60%, $main-color 40%);
    display: flex;
    justify-content : space-around;
    align-items: center;

    .intro {

        h3 {
            margin-bottom: 10px;
            color: $third-color;
        }

        h1 {
            font-size: 3em;
            margin-bottom: 10px;
            color: $third-color;
        }

        p {
            color: $third-color;
        }

        .jobTitle {
            font-size: 24px;
            color: $main-color;
        }

        a {
            text-decoration: none;
            color: $main-color;

            button {
                margin-top: 25px;
                box-sizing: border-box;
                appearance: none;
                background-color: transparent;
                border: 2px solid $main-color;
                border-radius: 0.6em;
                color: $main-color;
                cursor: pointer;
                display: flex;
                align-self: center;
                font-size: 1rem;
                font-weight: 400;
                line-height: 1;
                padding: 1.2em 2.8em;
                text-decoration: none;
                text-align: center;
                text-transform: uppercase;
                font-weight: 700;

                &:hover {
                    transition: box-shadow 300ms ease-in-out, color 300ms ease-in-out;
                    outline: 0;
                    box-shadow: 0 0 40px 40px $main-color inset;
                    color: $secondary-color;
                }
            }
        }
    }

    // Media query for smaller screens
    @media (max-width: 900px) {
        .intro {
            margin-right: 70px;
            p{
                display: none;
            }
        }
    }
}
